<template>
	<div class="contain">
		<header>
			<h2 class="titile"></h2>
		</header>
		<section>
			<article class="barTop">
				<ul>
					<li>()</li>
					<li>()</li>
					<li></li>
				</ul>
				<ul class="num">
					<li>{{limitMoney}}</li>
					<li>{{total}}</li>
					<li>{{rate}}</li>
				</ul>
			</article>
			<article>
				<div class="switch">
					<el-switch style="display: block" v-model="flag" active-color="#409EFF" inactive-color="#ff4949" active-text="" inactive-text="">
					</el-switch>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="compareName" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="companyNature" :disabled="!flag"></el-input>
					</div>
					<divp class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="name" :disabled="!flag"></el-input>
					</divp>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="registerCapital" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-date-picker class="inp" :disabled="!flag" v-model="registerTime" type="date" placeholder=""  value-format="yyyy-MM-dd">
						</el-date-picker>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="paidCapital" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="trade" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="businessScope" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item item4">
						<span class="bar"></span>
						<el-input class="inp" v-model="address" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
				</div>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-date-picker class="inp" :disabled="!flag" v-model="joinTime" type="date" placeholder=""  value-format="yyyy-MM-dd">
						</el-date-picker>
					</div>
					<div class="item">
						<span class="bar" style="width:98px;"></span>
						<el-input class="inp" v-model="tel" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar" style="width:98px;"></span>
						<el-input class="inp" v-model="idCard" :disabled="!flag"></el-input>
					</div>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<p class="item">
						<span class="bar">1</span>
						<el-input class="inp" v-model="clientName1" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">1</span>
						<el-input class="inp" v-model="occupyRate1" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">2</span>
						<el-input class="inp" v-model="clientName2" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">2</span>
						<el-input class="inp" v-model="occupyRate2" :disabled="!flag"></el-input>
					</p>
				</div>
				<div class="list clear">
					<p class="item">
						<span class="bar">3</span>
						<el-input class="inp" v-model="clientName3" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">3</span>
						<el-input class="inp" v-model="occupyRate3" :disabled="!flag"></el-input>
					</p>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankName" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankBranch" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankAccount" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankCardNum" :disabled="!flag"></el-input>
					</p>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="alimitInfo.limitMoney" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="rate" disabled="true"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="alimitInfo.monthNum" :disabled="!flag"></el-input>
					</p>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<div class="item" v-for="item in dialogImageUrl" :key="item">
						<uploadImg @url="getUrl" :dialogImageUrl="item.filePath+item.fileName" :fileName="item.fileName" :fileType="item.fileType" :flag="flag" :title="item.fileTypeStr"></uploadImg>
					</div>
					<!--<div class="item" v-for="item in returnImage" :key="item">
						<uploadImg @url="getUrl" 
								:dialogImageUrl="item.dialogImageUrl" 
								:flag="flag" :title='item.title'></uploadImg>
					</div>-->
				</div>
			</article>
		</section>
		<footer v-if="flag">
			<el-button size="small" @click="flag = false"></el-button>
			<el-button size="small" @click="editBase" type="primary"></el-button>
		</footer>
	</div>
</template>
<script>
	import uploadImg from '../../../../components/uploadImg/index'
	//
	let compareino = {
		name: '', //
	};

	export default {
		components: {
			"uploadImg": uploadImg
		},
		data() {
			return {
				flag: false,
				dialogImageUrl: [],
				limitMoney: '', //
				total: '', //
				rate: '', //
				monthNum: '', //
				compareName: '', //
				companyNature: '', //
				name: '', //
				registerCapital: '', //
				registerTime: '', //
				paidCapital: '', //
				trade: '', //
				businessScope: '', //
				address: '', //
				joinTime: '', //
				tel: '', //
				idCard:'',
				clientInfo: {}, //
				bankName: '', //
				bankBranch: '', //
				bankAccount: '', //
				bankCardNum: '', //
				//
				alimitInfo: {},
				imageList: [], //
				dialogFormVisible: false,
				formLabelWidth: '100px',
				imageList: [],
				form: {},
				currentIndex: '',
			}
		},
		methods: {
			//
			init() {
				let _this = this;
				console.log("" + _this.$API.CREDIT_MONEY);
				_this.$post(_this.$API.CREDIT_MONEY)
					.then((result) => {
						console.log("" + result.receivables);
						//
						if(null != result && result.code == "0") {
							_this.limitMoney = result.limitMoney;
							_this.rate = result.rate;
							_this.total = result.total;
						} else {
							_this.$message({
								message: '',
								type: 'warning'
							});
						}
					})
				console.log("" + this.$route.query.suppId);
				_this.$fetch(_this.$API.COMPARE_SUPPLIER_DETAIL, {
						"suppId": this.$route.query.suppId
					})
					.then((result) => {
						//
						if(null != result && result.code == "0") {
							if(null != result.supplier) {
								let getSupplierData = result.supplier;
								_this.compareName = getSupplierData.companyName;
								_this.companyNature = getSupplierData.companyNature;
								_this.name = getSupplierData.name;
								_this.registerCapital = getSupplierData.registerCapital;
								_this.registerTime = getSupplierData.registerTime;
								_this.paidCapital = getSupplierData.paidCapital;
								_this.trade = getSupplierData.trade;
								_this.businessScope = getSupplierData.businessScope;
								_this.address = getSupplierData.address;
								_this.joinTime = getSupplierData.joinTime;
								_this.tel = getSupplierData.tel;
								_this.idCard = getSupplierData.idCard;
								//
								_this.clientInfo = getSupplierData.proportionClient;
								let bToObj = JSON.parse(getSupplierData.proportionClient);
								if(null != bToObj) {
									_this.clientName1 = bToObj.clientName1;
									_this.occupyRate1 = bToObj.occupyRate1;
									_this.clientName2 = bToObj.clientName2;
									_this.occupyRate2 = bToObj.occupyRate2;
									_this.clientName3 = bToObj.clientName3;
									_this.occupyRate3 = bToObj.occupyRate3;
								}
								_this.bankName = getSupplierData.bankName;
								_this.bankBranch = getSupplierData.bankBranch;
								_this.bankAccount = getSupplierData.bankAccount;
								_this.bankCardNum = getSupplierData.bankCardNum;

								let accessories = result.accessories;
								let fileTypes = [1, 3, 4, 5, 6, 7];
								for(let i = 0; i < accessories.length; i++) {
									if(accessories[i].fileType == 1) {
										accessories[i].fileTypeStr = "";
										delete fileTypes[0];
									}
									if(accessories[i].fileType == 3) {
										accessories[i].fileTypeStr = "";
										delete fileTypes[1];
									}
									if(accessories[i].fileType == 4) {
										accessories[i].fileTypeStr = "";
										delete fileTypes[2];
									}
									if(accessories[i].fileType == 5) {
										accessories[i].fileTypeStr = "";
										delete fileTypes[3];
									}
									if(accessories[i].fileType == 6) {
										accessories[i].fileTypeStr = "";
										delete fileTypes[4];
									}
									if(accessories[i].fileType == 7) {
										accessories[i].fileTypeStr = "";
										delete fileTypes[5];
									}
								}

								for(let i = 0; i < fileTypes.length; i++) {
									let sizeNum = accessories.length + i;
									let fileArr = {};
									if(fileTypes[i] != null) {
										if(fileTypes[i] == 1) {
											fileArr.fileTypeStr = "";
											fileArr.fileType = 1;
											accessories.push(fileArr);
										}
										if(fileTypes[i] == 3) {
											fileArr.fileTypeStr = "";
											fileArr.fileType = 3;
											accessories.push(fileArr);
										}
										if(fileTypes[i] == 4) {
											fileArr.fileTypeStr = "";
											fileArr.fileType = 4;
											accessories.push(fileArr);
										}
										if(fileTypes[i] == 5) {
											fileArr.fileTypeStr = "";
											fileArr.fileType = 5;
											accessories.push(fileArr);
										}
										if(fileTypes[i] == 6) {
											fileArr.fileTypeStr = "";
											fileArr.fileType = 6;
											accessories.push(fileArr);
										}
										if(fileTypes[i] == 7) {
											fileArr.fileTypeStr = "";
											fileArr.fileType = 7;
											accessories.push(fileArr);
										}
									}
								}
								this.dialogImageUrl = result.accessories;

								if(null != result.alimit) {
									_this.alimitInfo.limitMoney = result.alimit.limitMoney;
									_this.alimitInfo.rate = result.alimit.rate;
									_this.alimitInfo.monthNum = result.alimit.monthNum;
								}
							}
						} else {
							_this.$message({
								message: '',
								type: 'warning'
							});
						}
					})

					.catch((error) => {
						_this.$message.error('');
					})
			},
			//
			editBase() {
				let _this = this;
				console.log("" + _this.name);
				if(null == _this.compareName || _this.compareName == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.companyNature || this.companyNature == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.name || _this.name == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.registerCapital || _this.registerCapital == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.registerTime || _this.registerTime == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.paidCapital || _this.paidCapital == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.trade || _this.trade == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.businessScope || _this.businessScope == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.address || _this.address == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.joinTime || _this.joinTime == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}

				if(null == _this.tel || _this.tel == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null==_this.idCard||_this.idCard==""){
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				if(null == _this.bankName || _this.bankName == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}

				if(null == _this.bankCardNum || _this.bankCardNum == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}
				console.log("" + _this.alimitInfo.limitMoney);
				/*if(null == _this.alimitInfo.limitMoney || _this.alimitInfo.limitMoney == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}*/
					console.log("" + _this.alimitInfo.limitMoney + "" + _this.limitMoney);
					if(_this.alimitInfo.limitMoney > _this.limitMoney) {
						_this.$message({
							message: '',
							type: 'warning'
						});
						return false;
					}
				/*if(null == _this.alimitInfo.rate || _this.alimitInfo.rate == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}*/
				if(null == _this.alimitInfo.monthNum || _this.alimitInfo.monthNum == "") {
					_this.$message({
						message: '',
						type: 'warning'
					});
					return false;
				}

				let viewImg = "";
				let dialogImageUrl = this.dialogImageUrl;
				for(let i = 0; i < dialogImageUrl.length; i++) {
					if(dialogImageUrl[i].fileName != null) {
						viewImg +=
							dialogImageUrl[i].fileType + ";" + dialogImageUrl[i].fileName + ",";
					}
				}
				console.log();
				let params = {
					compareName: _this.compareName, //
					companyNature: _this.companyNature, //
					name: _this.name, //
					registerCapital: _this.registerCapital, //
					registerTime: _this.registerTime, //
					paidCapital: _this.paidCapital, //
					trade: _this.trade, //
					businessScope: _this.businessScope, //
					address: _this.address, //
					joinTime: _this.joinTime, //
					tel: _this.tel, // 
					idCard:_this.idCard,
					clientInfo: {}, //
					bankName: _this.bankName, //
					bankBranch: _this.bankBranch, //
					bankAccount: _this.bankAccount, //
					bankCardNum: _this.bankCardNum, //
					limitMoney: _this.alimitInfo.limitMoney, //
					rate: _this.alimitInfo.rate, //
					monthNum: _this.alimitInfo.monthNum, //
					viewImgs: viewImg, //
				}
				_this.$post(_this.$API.ADD_SUPPLIER_INFO, params)
					.then((resultData) => {
						if(null != resultData && resultData.code == '0') {
							_this.$message({
								type: 'success',
								message: '!'
							})
						} else {
							_this.$message({
								type: 'success',
								message: resultData.msg
							})
						}
					})

			},

			handleEdit(index, row) { //
				this.form = {};
				if(index == 666) {
					this.dialogFormVisible = true
				} else {
					this.form = this.tableData[index]
					this.currentIndex = index
					this.dialogFormVisible = true
				}

			},
			handleDelete(index, row) { //
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.tableData.splice(index, 1)
					this.$message({
						type: 'success',
						message: '!'
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					})
				})
			},
			cancel() { //
				this.dialogFormVisible = false
			},
			defaultLoad(data) {
				return false;
			},
			update() { //
				this.form.date = reformat(this.form.date)
				this.tableData.push(this.form)
				this.dialogFormVisible = false
			},
			getUrl(data) {
				console.log(data)
				this.dialogImageUrl.map(item => {
					if(item.fileType == data.fileType) {
						item.fileName = data.fileName;
					}
				})
			},
			
		},
		created() {
			this.init(); //init
			let fileTypes = [1, 3, 4, 5, 6, 7];
			for(let i = 0; i < fileTypes.length; i++) {
				let fileArr = {};
				if(fileTypes[i] != null) {
					if(fileTypes[i] == 1) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 1;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 3) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 3;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 4) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 4;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 5) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 5;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 6) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 6;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 7) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 7;
						this.dialogImageUrl.push(fileArr);
					}
				}
			}
		},
	}
</script>
<style lang="scss" scoped type="text/css">
	@import "./index.scss";
</style>


