<template>
  <el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column
      type="index"
      :index="indexMethod">
    </el-table-column>
    <el-table-column
      prop="date"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label="">
    </el-table-column>
  </el-table>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333,
          tag: ''
        }, {
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333,
          tag: ''
        }, {
          date: '2016-05-04',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333,
          tag: ''
        }, {
          date: '2016-05-01',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333,
          tag: ''
        }],
      }
    },
    methods: {
      indexMethod(index) {
        return index * 2;
      }
    }
  };
</script>


