<template>
	<div class="contain">
		<header>
			<ul>
				<li>()</li>
				<li>()</li>
				<li></li>
				<li></li>
			</ul>
			<ul class="num">
				<li id="limitMoney">{{baseinfo.limitMoney}}</li>
				<li id="total">{{baseinfo.total}}</li>
				<li id="rate">{{baseinfo.rate}}</li>
				<li >{{baseinfo.monthNum}}</li>
			</ul>
		</header>
		<section>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<font class="inp" id="productArea" >{{baseinfo.productArea}}</font>
					</div>
					<div class="item">
						<span class="bar"></span>
						<font class="inp" id="productType">{{baseinfo.productType}}</font>
					</div>
					<div class="item">
						<span class="bar"></span>
						<font class="inp" id="factorType" >{{baseinfo.factorType}}</font>
					</div>
					<div class="item">
						<span class="bar"></span>
						<font class="inp" id="transferMoneyType">{{baseinfo.transferMoneyType}}</font>
					</div>
				</div>
			</article>
			<article>
				<div class="switch">
					<el-switch
						style="display: block"
						v-model="flag"
						active-color="#409EFF"
						inactive-color="#ff4949"
						active-text=""
						inactive-text="">
					</el-switch>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp"  v-model="baseinfo.companyName" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" id="companyNature" v-model="baseinfo.companyNature"  :disabled="!flag"></el-input>
					</div>
					<divp class="item">
						<span class="bar"></span>
						<el-input class="inp" id="registerCapital" v-model="baseinfo.registerCapital"  :disabled="!flag"></el-input>
					</divp>
					<div class="item">
						<span class="bar">()</span>
						<el-input class="inp" id="paidCapital" v-model="baseinfo.paidCapital" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp"id="legalPerson" v-model="baseinfo.legalPerson" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" id="contactTel" v-model="baseinfo.contactTel"  :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-date-picker class="inp" :disabled="!flag"
							v-model="baseinfo.registerTime"
							type="date"
							placeholder=""
      						value-format="yyyy-MM-dd">
						</el-date-picker>
					</div>
					
				</div>
				<div class="list clear">
					<div class="item item4">
						<span class="bar"></span>
						<el-input class="inp" id="registerAddress" v-model="baseinfo.registerAddress" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item item4 clear">
						<span class="bar"></span>
						<el-input class="inp textarea" id="businessScope" v-model="baseinfo.businessScope" :disabled="!flag" type="textarea"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item item4">
						<span class="bar"></span>
						<el-input class="inp textarea" id="corporateArchitecture" v-model="baseinfo.corporateArchitecture" :disabled="!flag" type="textarea"></el-input>
					</div>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="totalAssets" v-model="baseinfo.totalAssets"  :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="primaryIncome" v-model="baseinfo.primaryIncome" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="totalLiability" v-model="baseinfo.totalLiability" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="profit" v-model="baseinfo.profit" :disabled="!flag"></el-input>
					</p>
				</div>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="netAsset" v-model="baseinfo.netAsset" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="netProfits" v-model="baseinfo.netProfits" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="receivables" v-model="baseinfo.receivables" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="accountsPayable" v-model="baseinfo.accountsPayable" :disabled="!flag"></el-input>
					</p>
				</div>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="assetsLiabilitiesRate" v-model="baseinfo.assetsLiabilitiesRate"  :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="flowRate" v-model="baseinfo.flowRate" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="runCashFlow" v-model="baseinfo.runCashFlow" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="investCashFlow" v-model="baseinfo.investCashFlow" :disabled="!flag"></el-input>
					</p>
				</div>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" id="planCashFlow" v-model="baseinfo.planCashFlow" :disabled="!flag"></el-input>
					</p>
				</div>
			</article>
			<article>
				<div>
					<h3 class="title v_title"></h3>
					<el-button size="small" type="primary" v-if="flag" @click="handleEdit(-1)"><i class="fa fa-plus"></i>&nbsp;</el-button>
				</div>
				<div class="list clear">
					<el-table :data="tableData" style="width: 100%">
        				<el-table-column prop="unit" label="" width="180"></el-table-column>
        				<el-table-column prop="money" label="" width="180"></el-table-column>
        				<el-table-column prop="age" label=""></el-table-column>
						<el-table-column prop="formingReason" label=""></el-table-column>
        				<el-table-column label="" v-if="flag">
							<template scope="scope">
            					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
            					<el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          					</template>
        				</el-table-column>
      				</el-table>
    				<el-dialog title="" :visible.sync="dialogFormVisible">
      					<el-form :model="form" label-width="60%">
        					<el-form-item label="" :label-width="formLabelWidth">
          					<el-input v-model="form.unit" auto-complete="off"></el-input></el-form-item>
        					<el-form-item label="" :label-width="formLabelWidth">
          						<el-input v-model="form.money" auto-complete="off"></el-input>
        					</el-form-item>
        					<el-form-item label="" :label-width="formLabelWidth">
								<el-input v-model="form.age" auto-complete="off"></el-input>
        					</el-form-item>
        					<el-form-item label="" :label-width="formLabelWidth">
								<el-input  v-model="form.formingReason" auto-complete="off" type="textarea"></el-input>
        					</el-form-item>
      					</el-form>
						<div slot="footer" class="dialog-footer">
							<el-button @click="cancel"> </el-button>
							<el-button type="primary" @click="conform"> </el-button>
						</div>
    				</el-dialog>
				</div>
			</article>
		</section>
		<footer v-if="flag">
			<el-button  size="small" @click="flag = false"></el-button>
			<el-button  size="small" @click="submit()" type="primary" ></el-button>
		</footer>
	</div>
</template>
<script>
	
	import { reformat } from '../../../static/reformartDate'
	import fn from "@/static/common";
	export default {
		data(){
			return {
				baseinfo : {},
				flag : false ,
				tableData:[] ,
				dialogFormVisible: false,
				formLabelWidth: '100px',
				form: {},
				currentIndex: '',
				selectIndex:''
			}
		},
		components:{

		},
		methods :{
			conform(){
				this.dialogFormVisible = false;
			},
			init(){
				let _this = this ;
				_this.$fetch(this.$API.CREDIT_MONEY)
				.then((resultData) => {
					if(null!=resultData&&resultData.code=='0'){
						_this.baseinfo.limitMoney = resultData.limitMoney;
						_this.baseinfo.total = resultData.total;
						_this.baseinfo.rate = resultData.rate;
						//_this.baseinfo.monthNum = resultData.monthNum;
					}
				}),
				
				_this.$post(_this.$API.CORE_COMPARE_BASEINFO)
				.then((result) => {
					if(result.code == "0"){
						_this.baseinfo.id=result.info.id;
						_this.baseinfo.productArea = result.info.productArea ;
						//
						const productType = result.info.productType || "" ;
						if(productType == "1"){
							_this.baseinfo.productType = "" ;
						}else if(productType == "2"){
							_this.baseinfo.productType = "" ;
						}
						//
						const factorType = result.info.factorType ;
						switch (factorType) {
							case 1:	_this.baseinfo.factorType = "" ; break ;
							case 2:	_this.baseinfo.factorType = "" ; break ;	
							case 3:	_this.baseinfo.factorType = "" ; break ;
							case 4:	_this.baseinfo.factorType = "" ; break ;
							default: _this.baseinfo.factorType = "" ; break;
						}
						//
						const transferMoneyType = result.info.transferMoneyType ;
						switch (transferMoneyType) {
							case 1:	_this.baseinfo.factorType = "" ; break ;
							case 2:	_this.baseinfo.factorType = "" ; break ;	
							default: _this.baseinfo.factorType = "" ; break;
						}
						_this.baseinfo.companyName = result.info.companyName ;	//
						_this.baseinfo.companyNature = result.info.companyNature ;	//
						_this.baseinfo.registerCapital = result.info.registerCapital ;	//
						_this.baseinfo.paidCapital = result.info.paidCapital ;	//
						_this.baseinfo.legalPerson = result.info.legalPerson ;	//
						_this.baseinfo.contactTel = result.info.contactTel ;	//
						_this.baseinfo.registerTime = result.info.registerTime ;	//
						_this.baseinfo.registerAddress = result.info.registerAddress ;	//
						_this.baseinfo.businessScope = result.info.businessScope ;	//
						_this.baseinfo.corporateArchitecture = result.info.corporateArchitecture ;	//
						_this.baseinfo.totalAssets = result.info.totalAssets ;	//
						_this.baseinfo.primaryIncome = result.info.primaryIncome ;	//
						_this.baseinfo.totalLiability = result.info.totalLiability ;	//
						_this.baseinfo.profit = result.info.profit ;	//
						_this.baseinfo.netAsset = result.info.netAsset ;	//
						_this.baseinfo.netProfits = result.info.netProfits ;	//
						_this.baseinfo.receivables = result.info.receivables ;	//
						_this.baseinfo.accountsPayable =result.info.accountsPayable ;	//
						_this.baseinfo.assetsLiabilitiesRate = result.info.assetsLiabilitiesRate ;	//
						_this.baseinfo.flowRate = result.info.flowRate ;	//
						_this.baseinfo.runCashFlow = result.info.runCashFlow ;	//
						_this.baseinfo.investCashFlow = result.info.investCashFlow ;	//
						_this.baseinfo.planCashFlow = result.info.planCashFlow ;	//
						//
						let receivables = result.receivables || "" ;
						if(receivables){
							let creditInfo = receivables.map(item => {
								let object ={};
								object.id = item.id;
								object.unit = item.unit;
								object.money = item.money;
								object.age = item.age;
								object.formingReason = item.formingReason;
								return object ;
							}) ;
							_this.tableData = creditInfo;
						}
						
					}else{
						_this.$message({
							message: '',
							type: 'success'
						});
					}
				})
				.catch((error)=>{
					 _this.$message.error('');
				})
			},
			submit(){
				let _this = this ;
				if(null==_this.baseinfo.companyName.trim()||_this.baseinfo.companyName.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.companyNature.trim()||_this.baseinfo.companyNature.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.registerCapital.trim()||_this.baseinfo.registerCapital.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.paidCapital.trim()||_this.baseinfo.paidCapital.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.legalPerson.trim()||_this.baseinfo.legalPerson.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				
				if(null==_this.baseinfo.contactTel.trim()||_this.baseinfo.contactTel.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}if(null==_this.baseinfo.registerTime||_this.baseinfo.registerTime==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.registerAddress.trim()||_this.baseinfo.registerAddress.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.businessScope.trim()||_this.baseinfo.businessScope.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				
				if(null== _this.baseinfo.corporateArchitecture.trim()|| _this.baseinfo.corporateArchitecture.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.totalAssets.trim()||_this.baseinfo.totalAssets.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.primaryIncome.trim()||_this.baseinfo.primaryIncome.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.totalLiability.trim()||_this.baseinfo.totalLiability.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null== _this.baseinfo.profit.trim()|| _this.baseinfo.profit.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.netAsset.trim()||_this.baseinfo.netAsset.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.netProfits.trim()||_this.baseinfo.netProfits.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.receivables.trim()||_this.baseinfo.receivables.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.assetsLiabilitiesRate.trim()||_this.baseinfo.assetsLiabilitiesRate.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				if(null==_this.baseinfo.flowRate.trim()||_this.baseinfo.flowRate.trim()==""){
					_this.$message({
						message:'!',
						type:'warning'
					});
					return false;
				}
				
				let params = {
				id:_this.baseinfo.id,
				companyName:_this.baseinfo.companyName,//
				companyNature:_this.baseinfo.companyNature,//
				registerCapital: _this.baseinfo.registerCapital,//
				paidCapital: _this.baseinfo.paidCapital,//
				legalPerson: _this.baseinfo.legalPerson,//
				contactTel: _this.baseinfo.contactTel,//
				registerTime: _this.baseinfo.registerTime,//
				registerAddress: _this.baseinfo.registerAddress,//
				businessScope: _this.baseinfo.businessScope,//
				corporateArchitecture: _this.baseinfo.corporateArchitecture,//
				
				totalAssets: _this.baseinfo.totalAssets,//
				primaryIncome: _this.baseinfo.primaryIncome,//
				totalLiability: _this.baseinfo.totalLiability,//
				profit: _this.baseinfo.profit,//
				netAsset: _this.baseinfo.netAsset,//
				netProfits: _this.baseinfo.netProfits,//
				receivables: _this.baseinfo.receivables,//
				accountsPayable: _this.baseinfo.accountsPayable,//
				assetsLiabilitiesRate:_this.baseinfo.assetsLiabilitiesRate,//
				flowRate: _this.baseinfo.flowRate,//
				runCashFlow:_this.baseinfo.runCashFlow,//
				investCashFlow: _this.baseinfo.investCashFlow,//
				planCashFlow: _this.baseinfo.planCashFlow,//
				receiveStr:JSON.stringify(_this.tableData),
				};
				console.log(""+JSON.stringify(_this.tableData));
				/*_this.$fetch(_this.$API.SUBMIT_COMPARE_INFO,params)
				.then((resultData) => {
					if(null!=resultData&&resultData.code=='0'){
						_this.$message({
						type: 'success',
						message: '!'	
						})
					}else{
						_this.$message({
						type: 'warning',
						message: resultData.msg
						})
					}
				})*/
				
			},
			handleEdit(index, row) {    //
				this.form = {} ;
				if(index == -1){//
					this.selectIndex = index;
					this.dialogFormVisible = true
				}else{//
					this.form = this.tableData[index];
					this.currentIndex = index;
					this.selectIndex = index;
					this.dialogFormVisible = true
				}
				
				
			},
			handleDelete(index, row) {  //
				//this.tableData = {id:row.id}
				console.log("id"+row.id)
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.tableData.splice(index, 1)
					this.$message({
					type: 'success',
					message: '!'
					})
				}).catch(() => {
					this.$message({
					type: 'info',
					message: ''
					})
				})
			},
			cancel() {      //
				this.dialogFormVisible = false
			},
			conform() {      //
				//this.form.date = reformat(this.form.date)
				//this.tableData.push(this.form)
				let _this = this;
				console.log(""+_this.selectIndex)
				if(null!=_this.selectIndex){
					if(_this.selectIndex==-1){
						this.tableData.push(this.form)
					}else{
						
					}
				}
				this.dialogFormVisible = false
			},
		},
		created(){
			this.init();
		}
	}

</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>




