<template>
	<div class="contain">
		<header>
			<el-input class="inp" v-model="key" placeholder=""></el-input>
			<el-button type="primary" plain icon="el-icon-search" size="mini"></el-button>
			<el-button type="success" plain icon="el-icon-plus" size="mini" @click="add"></el-button>
		</header>
		<section>
			<el-table :data="tableData" stripe style="width: 100%">
    			<el-table-column prop="supCompanyName" label="" width="100"></el-table-column>
    			<!--<el-table-column prop="tel" label="" ></el-table-column>
    			<el-table-column prop="joinTime" label="" width="120" ></el-table-column>-->
				<el-table-column prop="businessScope" label="" width="100"></el-table-column>
				<el-table-column prop="address" label=""></el-table-column>
				<el-table-column prop="creditM" label="" width="100"></el-table-column>
				<el-table-column prop="status" label=""width="60"></el-table-column>
				<el-table-column prop="modifyTime" label="" width="150"></el-table-column>
				<el-table-column fixed="right" label="" width="350">
					<template slot-scope="scope">
						<!-- <el-button 	@click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small"></el-button> -->
						<el-button size="mini" type="default" class="el-button el-button--info el-button--mini is-plain" plain @click="view(scope.row.id)"></el-button>
						<el-button size="mini" type="success" plain @click="agree(scope.row.id,1,scope.row.creditLimit)" v-show="scope.row.status==''"></el-button>
						<el-button size="mini" type="danger" plain @click="refuser(scope.row.id,2,scope.row.creditLimit)" v-show="scope.row.status==''"></el-button>
						<el-button size="mini" type="warning" plain @click="handleEdit(scope.row.id,scope.row)" v-if="scope.row.userId==null" ></el-button>
						<el-button size="mini" type="warning" plain @click="handleEdit2(scope.row.userId)" v-if="scope.row.userId!=null"></el-button>
					</template>
				</el-table-column>
  			</el-table>
			<el-dialog title="" :visible.sync="dialogFormVisible">
				<el-form :model="form">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="form.username"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input type="password" v-model="form.password"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input type="password" v-model="form.conPassword"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="cancel"> </el-button>
					<el-button type="primary" @click="update"> </el-button>
				</div>
    		</el-dialog>
    		
		</section>
		<footer>
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 30, 50]" :page-size="limitSize" layout="total, sizes, prev, pager, next, jumper" :total="totalCount">
			</el-pagination>
		</footer>
	</div>
</template>
<script>
	import { reformat } from '../../../../static/reformartDate'	
	import API from "@/config/api";
	let supplierInfo = [];
	export default {
		data(){
			let paramId = '';
			return {
				dialogFormVisible: false,
				formLabelWidth: '80px',
				form: {},
				currentIndex: '',
				tableData:supplierInfo,
				key:"",//
				
			}
		},
		components:{

		},
		methods: {
			add(){
				const self = this ;
				self.$store.dispatch("nav/changeBack", true);
				self.$router.push({
					"path":"eadd"
				})
			},
			lists(){
			let _this = this ;
			let currentPage = _this.currentPage;//
      		let limitSize = _this.limitSize;//
      		let totalCount = _this.totalCount;
      		//
      		if(null==currentPage||currentPage==""){
      			currentPage = 1;
      		}
      		if(null==limitSize||limitSize==""){
      			limitSize = 20;
      		}
      		if(null==totalCount||totalCount==""){
      			totalCount = 0;
      		}
      		let params ={
      			type: 2,
		        page: currentPage,
		        limit: limitSize,
		        key: _this.key
      		}
			let path =_this.$API.COMPARE_SUPPLIER_LIST+"&page=1&limit=10" 
			_this.$post(path).then((result) => {
					if(null!=result&&result.code=='0'){
						  let getData  = result.page.list;
						  supplierInfo = getData;
						   if(null!=supplierInfo){
						  	for(let i=0;i<supplierInfo.length;i++){
						  		let getSate = supplierInfo[i].status;
						  		//'012'
						  		if(null!=getSate&&getSate=='0'){
						  			supplierInfo[i].status="";
						  		}else if(null!=getSate&&getSate=='1'){
						  			supplierInfo[i].status="";
						  		}else if(null!=getSate&&getSate=='2'){
						  			supplierInfo[i].status="";
						  		}
						  }
						}
						   this.tableData= supplierInfo;
				          _this.totalCount = result.page.totalCount;
				          _this.limitSize = result.page.pageSize;
				          _this.currentPage = result.page.currPage;
					}
				});
			},
			agree(id,state,creditLimit){
				let _this = this;
				let  params ={supId:id,status:state,creditM:creditLimit};
				_this.$fetch(_this.$API.COMPARE_SUPPLIER_AUDIT,params)
				.then((resultData) => {
					if(null!=resultData&&resultData.code=='0'){
						_this.$message({
						type: 'success',
						message: '!'	
						})
					}else{
						_this.$message({
						type: 'warning',
						message: resultData.msg
						})
					}
				})
				
			},
			refuser(id,state,creditLimit){
				let _this = this;
				let  params ={supId:id,status:state,creditM:creditLimit};
				_this.$fetch(_this.$API.COMPARE_SUPPLIER_AUDIT,params)
				.then((resultData) => {
					if(null!=resultData&&resultData.code=='0'){
						_this.$message({
						type: 'success',
						message: '!'	
						})
					}else{
						_this.$message({
						type: 'warning',
						message: resultData.msg
						})
					}
				})
			},
			view(id){		//
				let _this = this ;
				const self = this ;
				self.$router.push({
					"path":"eview",
					query: {suppId: id}})
			},
			 seek() {
			  this.lists();
		      this.currentPage = 1;
		    },
			handleEdit(id,row) {    //
				this.form = { suppId: id };
      			this.form.username = row.username;
				this.dialogFormVisible = true;
				
			},
			handleEdit2(userId) {    //
				let _this = this ;
				this.$confirm("?", "", {
		        confirmButtonText: "",
		        cancelButtonText: "",
		        type: "warning"
		      })
		        .then(() => {
		         let params = {userId:userId};
		          _this.$fetch(_this.$API.POST_SYS_RESET, params).then(result => {
		            if (result.code == "0") {
		              this.$message({
		                message: "",
		                type: "success"
		              });
		              this.lists();
		            } else {
		              this.$message({
		                message: result.msg,
		                type: "warning"
		              });
		            }
		          });
		        })
		        .catch(() => {
		          this.$message({
		            type: "info",
		            message: ""
		          });
		        });
			},
			cancel() {      //
				this.dialogFormVisible = false
			},
			update() {      //  
				let _this =this;
				if(null==_this.form.username||_this.form.username==""){
					_this.$message({
					type: 'warning',
					message: '!'	
					})
					return false;
				}
				if(null==_this.form.password||_this.form.password==""){
					_this.$message({
					type: 'warning',
					message: '!'	
					})
					return false;
				}
				if(null==_this.form.conPassword||_this.form.conPassword==""){
					this.$message({
					type: 'warning',
					message: '!'	
					})
					return false;
				}
				if(null!=_this.form.password &&null!=_this.form.conPassword){
					if(_this.form.password !=_this.form.conPassword){
						this.$message({
						type: 'warning',
						message: '!'	
						})
						return false;
						}
				}
				let params ={
					suppId:_this.form.suppId,
					username:_this.form.username,
					password:_this.form.password
				}
				_this.$fetch(this.$API.POST_SYS_CRTA,params)
				.then((resultData) => {
					if(null!=resultData&&resultData.code=='0'){
						_this.$message({
						type: 'warning',
						message: ''	
						})
						location.reload();
					}else{
						_this.$message({
						type: 'warning',
						message: resultData.msg
						})
					}
				});
				this.lists();
				this.dialogFormVisible = false;
				
			},
      		deleteRow(index, rows) {	//
				rows.splice(index, 1);
			},
			handleSizeChange(val) {		//
				console.log(` ${val} `);
			},
			handleCurrentChange(val) {		//
				console.log(`: ${val}`);
			}
    },
	    created() {
	    this.lists();
	  	}
	}
	

</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>



