<template>
	<div class="content">
		 <div id="allmap" ref="allmap"></div>  
	</div>
</template>
<script>  
export default {  
  name: 'App',  
  methods:{  
    map(){  
      let map =new BMap.Map(this.$refs.allmap); // Map  
     map.centerAndZoom('',5);
	var data_info = [
					 [120.1405982234,30.3017117165,"552403"],
					// [116.412222,39.912345,"5"]
					];
	var opts = {
				width : 250,     // 
				height: 80,     // 
				title : "" , // 
				enableMessage:true,//
			   };
	//
	var infoWindow1 = new BMap.InfoWindow("552403", opts); 
	var point1 = new BMap.Point(120.1405982234,30.3017117165);
	map.openInfoWindow(infoWindow1,point1); //
	
	for(var i=0;i<data_info.length;i++){
		var marker = new BMap.Marker(new BMap.Point(data_info[i][0],data_info[i][1]));  // 
		var content = data_info[i][2];
		map.addOverlay(marker);               // 
		addClickHandler(content,marker);
		
	}
	function addClickHandler(content,marker){
		marker.addEventListener("click",function(e){
			openInfo(content,e)
		});
	}
	function openInfo(content,e){
		var p = e.target;
		var point = new BMap.Point(p.getPosition().lng, p.getPosition().lat);
		var infoWindow = new BMap.InfoWindow(content,opts);  //  
		map.openInfoWindow(infoWindow,point); //
	}
	
	
	(function setMapEvent(){
        map.enableDragging();//()
        map.enableScrollWheelZoom();//
        map.enableDoubleClickZoom();//()
        map.enableKeyboard();//
   })()
    }  
  
  },  
  mounted(){  
    this.map()  
  
  }  
}  
</script>  
  
<style lang="scss" scoped type="text/css">

#allmap{  
  height: 100%;  
  overflow: hidden;  
  
}  
</style>


