<template>
	<div class="contain">
		<header>
			<h2 class="titile"></h2>
		</header>
		<section>
			<article class="barTop">
				<ul>
					<li>()</li>
					<li>()</li>
					<li></li>
				</ul>
				<ul class="num">
					<li>{{limitMoney}}</li>
					<li>{{total}}</li>
					<li>{{rate}}</li>
				</ul>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="compareName" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="companyNature" :disabled="!flag"></el-input>
					</div>
					<divp class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="name" :disabled="!flag"></el-input>
					</divp>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="registerCapital" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-date-picker class="inp" :disabled="!flag" v-model="registerTime" type="date" placeholder=""  value-format="yyyy-MM-dd">
						</el-date-picker>
						<!--<span class="bar"></span>
						<el-input class="inp" v-model="registerTime" :disabled="!flag"></el-input>-->
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="paidCapital" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="trade" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="businessScope" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item item4">
						<span class="bar"></span>
						<el-input class="inp" v-model="address" :disabled="!flag"></el-input>
					</div>
				</div>
				<div class="list clear">
					<div class="item">
						<span class="bar"></span>
						<el-date-picker class="inp" :disabled="!flag" v-model="joinTime" type="date" placeholder=""  value-format="yyyy-MM-dd">
						</el-date-picker>
						<!--<span class="bar"></span>
						<el-input class="inp" v-model="joinTime" :disabled="!flag"></el-input>-->
					</div>
					<div class="item">
						<span class="bar" style="width:98px;"></span>
						<el-input class="inp" v-model="tel" :disabled="!flag"></el-input>
					</div>
					<div class="item">
						<span class="bar" style="width:98px;"></span>
						<el-input class="inp" v-model="idCard" :disabled="!flag"></el-input>
					</div>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<p class="item">
						<span class="bar">1</span>
						<el-input class="inp" v-model="clientInfo.clientName1" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">1</span>
						<el-input class="inp" v-model="clientInfo.occupyRate1" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">2</span>
						<el-input class="inp" v-model="clientInfo.clientName2" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">2</span>
						<el-input class="inp" v-model="clientInfo.occupyRate2" :disabled="!flag"></el-input>
					</p>
				</div>
				<div class="list clear">
					<p class="item">
						<span class="bar">3</span>
						<el-input class="inp" v-model="clientInfo.clientName3" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar">3</span>
						<el-input class="inp" v-model="clientInfo.occupyRate3" :disabled="!flag"></el-input>
					</p>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankName" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankBranch" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankAccount" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="bankCardNum" :disabled="!flag"></el-input>
					</p>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="creadit" :disabled="!flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="rate" :disabled="flag"></el-input>
					</p>
					<p class="item">
						<span class="bar"></span>
						<el-input class="inp" v-model="limitNum" :disabled="!flag"></el-input>
					</p>
				</div>
			</article>
			<article>
				<h3 class="title"></h3>
				<div class="list clear">
					<div class="item" v-for="item in dialogImageUrl" :key="item">
						<uploadImg @url="getUrl" :dialogImageUrl="item.filePath+item.fileName" :fileName="item.fileName" :fileType="item.fileType" :flag="flag" :title="item.fileTypeStr"></uploadImg>
					</div>
				</div>
			</article>
		</section>
		<footer v-if="flag">
			<el-button size="small" @click="flag = false"></el-button>
			<el-button size="small" @click="submit(flag)" type="primary"></el-button>
		</footer>
	</div>
</template>
<script>
	import uploadImg from '../../../../components/uploadImg/index'

	export default {
		components: {
			"uploadImg": uploadImg
		},
		data() {

			return {
				flag: true,
				dialogImageUrl: [],
				tableData: [],
				dialogFormVisible: false,
				formLabelWidth: '100px',
				form: {},
				currentIndex: '',

				limitMoney: '', //
				total: '', //
				rate: '', //
				compareName: '', //
				companyNature: '', //
				name: '', //
				idCard:'',//
				registerCapital: '', //
				registerTime: '', //
				paidCapital: '', //
				trade: '', //
				businessScope: '', //
				address: '', //
				joinTime: '', //
				tel: '', //
				clientInfo: {}, //
				bankName: '', //
				bankBranch: '', //
				bankAccount: '', //
				bankCardNum: '', //
				creadit: '', //
				limitRate: '', //
				limitNum: '', //
				imageList: [], //
			}
		},
		methods: {
			init() {
				let _this = this;
				console.log("" + _this.$API.CREDIT_MONEY);
				_this.$fetch(this.$API.CREDIT_MONEY)
					.then((resultData) => {
						if(null != resultData && resultData.code == '0') {
							console.log("" + resultData + "" + resultData.total);
							_this.limitMoney = resultData.limitMoney;
							_this.total = resultData.total;
							_this.rate = resultData.rate;
						}
					});

			},
			submit(flag) {
				flag = true;
				let _this = this;
				console.log("" + _this.compareName);
				if(_this.compareName.trim() == null || _this.compareName.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.companyNature.trim() == null || _this.companyNature.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.name.trim() == null || _this.name.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.registerCapital.trim() == null || _this.registerCapital.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}

				if(_this.registerTime == null || _this.registerTime == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}

				if(_this.paidCapital.trim() == null || _this.paidCapital.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.trade.trim() == null || _this.trade.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.businessScope.trim() == null || _this.businessScope.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}

				if(_this.address.trim() == null || _this.address.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.joinTime == null || _this.joinTime == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.tel.trim() == null || _this.tel.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(null==_this.idCard.trim()||_this.idCard.trim()==""){
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				//
				console.log("" + _this.clientInfo);
				if(_this.bankName.trim() == null || _this.bankName.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.bankAccount.trim() == null || _this.bankAccount.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.bankCardNum.trim() == null || _this.bankCardNum.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				if(_this.creadit.trim() == null || _this.creadit.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				} else {
					console.log("" + _this.creadit + "" + _this.limitMoney + "" + _this.total);
					if(_this.creadit > _this.limitMoney) {
						_this.$message({
							type: 'warning',
							message: '!'
						})
						return false;
					}

				}
				if(_this.limitNum.trim() == null || _this.limitNum.trim() == "") {
					_this.$message({
						type: 'warning',
						message: '!'
					})
					return false;
				}
				let viewImg = "";
				let dialogImageUrl = this.dialogImageUrl;
				for(let i = 0; i < dialogImageUrl.length; i++) {
					if(dialogImageUrl[i].fileName != null) {
						viewImg +=
							dialogImageUrl[i].fileType + ";" + dialogImageUrl[i].fileName + ",";
					}
				}
				console.log("" + _this.clientInfo);
				let params = {
					type:2,
					companyName: _this.compareName, //
					companyNature: _this.companyNature, //
					name: _this.name, //
					registerCapital: _this.registerCapital, //
					registerTime: _this.registerTime, //
					paidCapital: _this.paidCapital, //
					trade: _this.trade, //
					businessScope: _this.businessScope, //
					address: _this.address, //
					joinTime: _this.joinTime, //
					tel: _this.tel, // 
					idCard :_this.idCard,
					proportionClient: JSON.stringify(_this.clientInfo), //
					bankName: _this.bankName, //
					bankBranch: _this.bankBranch, //
					bankAccount: _this.bankAccount, //
					bankCardNum: _this.bankCardNum, //
					limitMoney: _this.creadit, //
					rate: _this.rate, //
					monthNum: _this.limitNum, //
					viewImgs: viewImg
				}
				console.log("" + _this.compareName + "" + _this.joinTime);
				console.log("" + _this.$API.ADD_SUPPLIER_INFO, params);
				_this.$fetch(_this.$API.ADD_SUPPLIER_INFO, params)
					.then((resultData) => {
						if(null != resultData && resultData.code == '0') {
							_this.$message({
								type: 'success',
								message: '!'
							})
							this.$router.back(-1);
						} else {
							_this.$message({
								type: 'warning',
								message: resultData.msg
							})
						}
					})

			},
			handleEdit(index, row) { //
				this.form = {};
				if(index == 666) {
					this.dialogFormVisible = true
				} else {
					this.form = this.tableData[index]
					this.currentIndex = index
					this.dialogFormVisible = true
				}

			},
			handleDelete(index, row) { //
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.tableData.splice(index, 1)
					this.$message({
						type: 'success',
						message: '!'
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					})
				})
			},
			cancel() { //
				this.dialogFormVisible = false
			},
			defaultLoad(data) {
				return false;
			},
			update() { //
				this.form.date = reformat(this.form.date)
				this.tableData.push(this.form)
				this.dialogFormVisible = false
			},
			getUrl(data) {
				console.log(data)
				this.dialogImageUrl.map(item => {
					if(item.fileType == data.fileType) {
						item.fileName = data.fileName;
					}
				})
			}
		},
		created() {
			this.init(); //init
			let fileTypes = [1, 3, 4, 5, 6, 7];
			for(let i = 0; i < fileTypes.length; i++) {
				let fileArr = {};
				if(fileTypes[i] != null) {
					if(fileTypes[i] == 1) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 1;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 3) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 3;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 4) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 4;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 5) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 5;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 6) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 6;
						this.dialogImageUrl.push(fileArr);
					}
					if(fileTypes[i] == 7) {
						fileArr.fileTypeStr = "";
						fileArr.fileType = 7;
						this.dialogImageUrl.push(fileArr);
					}
				}
			}
		},

	}
</script>
<style lang="scss" scoped type="text/css">
	@import "./index.scss";
</style>


