import Vue from 'vue'
import Router from 'vue-router'
import Test from '@/views/test/index'
import Login from '@/views/login/index' //
import Index from '@/views/index' //
import Home from '@/views/home/index' //
import BaseInfo from '@/views/enterprisePlatform/baseInfo/index' //---
import FinancingList from '@/views/enterprisePlatform/Financinglist/index' //---
import FinancingListView from '@/views/enterprisePlatform/Financinglist/view/index' //---()
import Enterprise from '@/views/enterprisePlatform/supplierList/enterprise/index' //---
import Eview from '@/views/enterprisePlatform/supplierList/enterprise/view/index' //---
import Eadd from '@/views/enterprisePlatform/supplierList/enterprise/add/index' //---
import Personal from '@/views/enterprisePlatform/supplierList/personal/index' //---()
import Pview from '@/views/enterprisePlatform/supplierList/personal/view/index' //---()
import Padd from '@/views/enterprisePlatform/supplierList/personal/add/index' //---()
import FinancingManage from '@/views/supplierPlatform/financingManage/index' //---
import FinancingManageView from '@/views/supplierPlatform/financingManage/view/index' //---
import FinancingManageAdd from '@/views/supplierPlatform/financingManage/add/index' //---
import EbaseInfo from '@/views/supplierPlatform/baseInfo/enterprise/index' //---
import PbaseInfo from '@/views/supplierPlatform/baseInfo/personal/index' //---


Vue.use(Router)
const routes = [
  {
    path: "/",
    name:"",
    component: Login,
  },
  {
    path: "/login",
    name: "",
    component: Login
  },
  {
    path: "/test",
    name: "",
    component: Test
  },
  {
    path: "/index",
    name: "",
    component: Index,
    meta: {
      barF: "",
      bar: "",
      barC: "fa-home"
    },
    redirect: "/home",
    children:[
      {
        path: "/home",
        name: "",
        component: Home,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/baseInfo",
        name: "-",
        component: BaseInfo,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/financingList",
        name: "-",
        component: FinancingList,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/financingListView",
        name: "-",
        component: FinancingListView,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/financingManage",
        name: "-",
        component: FinancingManage,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/financingManageView",
        name: "-",
        component: FinancingManageView,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/financingManageAdd",
        name: "-",
        component: FinancingManageAdd,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/enterprise",
        name: "-",
        component: Enterprise,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/eview",
        name: "-",
        component: Eview,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/eadd",
        name: "-",
        component: Eadd,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/personal",
        name: "-",
        component: Personal,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/pview",
        name: "-()",
        component: Pview,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
      {
        path: "/padd",
        name: "-()",
        component: Padd,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
       {
         path: "/ebaseInfo",
         name: "---",
         component: EbaseInfo,
         meta: {
           barF: "",
           bar: "",
           barC: "fa-home"
         }
       },
      {
        path: "/pbaseInfo",
        name: "---",
        component: PbaseInfo,
        meta: {
          barF: "",
          bar: "",
          barC: "fa-home"
        }
      },
    ]
  },
];
export default new Router({
  routes
})



